export default {
	name: 'adTabbarItem',
	components: {},
	props: {
		// 绑定页面
		dataCur: {
			type: String,
			default: ""
		},
		// 图标
		icon: {
			type: String,
			default: ""
		},
		// 文字
		text: {
			type: String,
			default: ""
		},
		// 文字颜色
		textColor: {
			type: String,
			default: "#5E5E5E"
		},
		// 文字大小
		textSize: {
			type: String,
			default: "22",
		}
	},
	methods: {
		// TabItem点击事件
		onClick(e) {
			this.$emit('click', e);
		}
	}
}
